import Vue from 'vue'
import Router from 'vue-router'
let Home = (res) => {require(['../components/home.vue'],res)}
let Login = (res) => {require(['../components/login.vue'],res)}
Vue.use(Router)

const router =  new Router({
  routes: [
    {
      path:'/',
      redirect:'/login'
    },
    {
      path:'/login',
      component:Login
    },
    {
      path:'/home',
      component:Home
    }
  ]
})

//挂载路由导航守卫
router.beforeEach((to,from,next) => {
  //to  将要访问得路径
  //from 从哪个路径跳转而来
  //next 函数 放行
  //  next()   next('/login')
  var token = localStorage.getItem('token');
  if(to.path === '/login'){
    if(token){
      return next('/home')
    }
    return next()
  };    
  if(!token){
    return next('/login')
  }else{
    next()
  }
})

export default router



